import json from "./Characters.JSON";
export default {
  name: "Characters",
  props: {
    type: String
  },
  data() {
    return {
      info: json
    };
  },
  mounted() {
  },
  methods: {
    openDemo(demo) {
      window.open(demo, "_blank");
    },
    getImgUrl() {
      if (this.$route.params.name === "sova") {
        return require("../../assets/characters/" +
          this.$route.params.name +
          ".webp");
      }
      return require("../../assets/characters/" +
        this.$route.params.name +
        ".png");
    }
  },
  components: {}
};
